.header {
  display: none;
}

.login {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  overflow-x: hidden;

  .burgerMenuButton {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    cursor: pointer;
    display: none;
    margin-right: 45px;
  }
  .burgerMenuWrapper {
    display: none;
  }
  .burgerMenu {
    display: none;
  }

  .adaptive {
    display: none;
  }

  .lines {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
    opacity: 0.2;
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
  }

  .left {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 40%;
    padding-left: 10%;
    margin-top: 2%;

    .top {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      a {
        color: rgba(255, 255, 255, 0.5);
        text-align: center;
        font-family: "Rubik";
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        text-decoration: none;
      }
    }

    .main {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      width: 100%;
      margin-top: 20%;

      h5 {
        color: rgba(255, 255, 255, 0.5);
        font-family: "Rubik";
        font-size: 24px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        margin: 0%;
      }

      h3 {
        color: #fff;
        font-family: "Rubik";
        font-size: 45px;
        font-style: normal;
        font-weight: 600;
        line-height: normal;
      }

      p {
        color: rgba(255, 255, 255, 0.5);
        font-family: "Rubik";
        font-size: 24px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
        margin: 0%;
      }

      a {
        color: #d6b9fc;
        font-family: "Rubik";
        font-size: 24px;
        font-style: normal;
        font-weight: 300;
        line-height: normal;
        text-decoration: none;
      }

      a:hover {
        text-decoration: underline;
      }
      a:active {
        text-decoration: underline;
      }

      .link {
        width: 100%;
      }

      form {
        width: 100%;

        .error.block input:focus {
          outline: 2px solid #FF4040 !important;
          border-radius: 10px;
        }
        .error.block input {
          outline: 2px solid #FF4040 !important;
          border-radius: 10px;
}

        .block {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          border-radius: 10px;
          background: rgba(214, 185, 252, 0.3);
          // padding: 2%;
          margin-top: 3%;
          height: 80px;
          position: relative;

          label {
            color: rgba(255, 255, 255, 0.5);
            font-family: "Rubik";
            font-size: 15px;
            font-style: normal;
            font-weight: 300;
            line-height: normal;
            position: absolute;
            top: 14px;
            left: 14px;
          }

          input {
            background: none;
            border: none;
            color: #fff;
            font-family: "Rubik";
            font-size: 24px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            width: 100%;
            height: 100%;
            padding-top: 2%;
            padding-left: 13px;
            padding-right: 60px;
            box-sizing: border-box;
          }

          input::-webkit-input-placeholder {
            color: #fff;
            font-family: "Rubik";
            font-size: 24px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            background: none;
          }

          input:-internal-autofill-selected {
            background-color: none !important;
          }

          .tick, .question {
            position: absolute;
            right: 25px;
            opacity: 1;
            pointer-events: auto;
            top: calc(50% - (30px / 2));
            width: 30px;
            height: 30px;
          }

        }
        .none {
          .tick, .question {
            opacity: 0;
          }
          .message {
            display: none;
          }
        }
        .error {
          .message {
            display: none;
          }
        }
        .check {
          .tick {
            stroke:#EAEAEA;
            stroke-width:2;
          }
          .question {
            fill: #FFF;
          }
          .message {
            display: none;
          }
        }
        .complete {
          .tick {
            stroke-width: 2px;
            stroke: #27A342;
          }
          .message {
            display: none;
          }
        }
        .error {
          // outline: 2px solid #FF4040;
          position: relative;

          .tick {
            stroke-width: 2px;
            stroke: #FF4040;  
          }

          .question {
            fill: #FF4040;
          }

          .message {
            //display: none;
            opacity: 0;
            transition: opacity .2s ease-in-out;
            pointer-events:none;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            position: absolute;
            background-color: #fff;
            border-radius: 10px;
            padding: 16px 10px;
            bottom: 80%;
            box-sizing: border-box;
            right: 0px;
            width: auto;
            p
              {
              max-width: 290px;
              color: #000;
              font-family: "Rubik";
              font-size: 16px;
              font-style: normal;
              font-weight: 300;
              line-height: normal;
              text-align: center;
            }

            img {
              position: absolute;
              bottom: -14px;
              right: 8px;
            }
          }
        }

        .error svg:hover {
          cursor:pointer
        }

        .error svg:hover ~ .message, .error svg:active ~ .message{
            opacity: 1;
            pointer-events: auto;
        }

        .error.block input:focus {
          outline: 2px solid #FF4040 !important;
        }

        .error:hover{
          outline: none!important;
        }
        

        .block:hover {
          outline: 1px solid #d6b9fc;
        }
        
        .block input:focus-visible {
          outline: 1px solid #d6b9fc;
          border-radius: 10px;
          -webkit-border-radius: 10px;
          -moz-border-radius: 10px;
          -ms-border-radius: 10px;
          -o-border-radius: 10px;
}
      }

      button {
        color: #fff;
        font-family: "Rubik";
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;
        border-radius: 10px;
        background: rgba(214, 185, 252, 0.7);
        border: none;
        width: 100%;
        padding: 2%;
        margin-top: 3%;
        cursor: pointer;
        height: 80px;
      }

      .steam {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        border-radius: 10px;
        background: rgba(42, 71, 94, 0.53) !important;

        p {
          margin: 0%;
          color: #fff;
          font-family: "Rubik";
          font-size: 20px;
          font-style: normal;
          font-weight: 500;
          line-height: normal;
          text-transform: uppercase;
          min-width: 150px;
          width:100%;
          height: 30px;
        }

        img {
          // margin-left: 3%;
        }
      }
      // .empty {
      //   width: 35px;
      //   overflow: hidden;
      //   transition: width 4s ease-in-out;
      // }
      .steam_text{
        width: 37%;
        overflow: hidden;
        transition: width 0.2s ease-in-out;
        -webkit-transition: width 0.2s ease-in-out;
        -moz-transition: width 0.2s ease-in-out;
        -ms-transition: width 0.2s ease-in-out;
        -o-transition: width 0.2s ease-in-out;
}
      .steam:hover .steam_text {
        width: 0%;
      }
    }
  }
  .right {
    // margin-left: 5%;
    height: 100vh;
    width: 40%;
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }
}

@media (max-width: 1380px) {
  .login {
    .left {
      .main {
        .steam {
          .steam_text {
            width: 55%;
          }
        }
      }
    }
  }
  
  .right {
    width: 40% !important;
  }
  .left {
    width: 50% !important;
    padding-right: 5%;
    padding-left: 5% !important;

    .top {
      width: 100%;
    }
    h5 {
      font-size: 20px !important;
    }

    h3 {
      font-size: 35px !important;
    }

    .main {
      .block {
        label p {
          font-size: 12px !important;
        }

        input {
          font-size: 20px;
        }

        input::-webkit-input-placeholder {
          font-size: 20px !important;
        }
      }

      button {
        margin-top: 5% !important;
      }
    }
  }
}

@media (max-width: 768px) {
  .login {
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 5%;

    .lines {
      display: none;
    }

    .right {
      display: none;
    }

    .adaptive {
      display: block;
      width: 100vw;
      margin-top: 0px;

      img {
        width: 100%;
      }
    }

    .left {
      width: 70% !important;
      margin-top: 0px;
      margin-bottom : 0px;
      .main {
        margin-top: 32px;
        form {
          .block {
            margin-top: 32px !important;
          }
        }
        a {
          button {
            margin-top: 32px !important;
          }
        }
      }
    }

    .top {
      width: 143% !important;
      .logo {
        display: flex;
        flex-direction: column;
        justify-content: center;
        img {
          width: 60%;
        }
      }
    }

    .main {
      align-items: center !important;
      text-align: center;
      width: 100%;

      p {
        width: 100%;
      }
    }
  }

  .top a {
    display: none;
  }

  .burgerMenuButton {
    display: block !important;
    position: relative;
    z-index: 100;
  }
  .burgerMenuWrapper {
    top: 0;
    left: 0;
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: block !important;
    z-index: 50;
    overflow: hidden;
  }
  .burgerMenu {
    position: absolute;
    z-index: 100;
    right: 25% !important;
    top: 120px;
    right: 130px;
    display: flex !important;
    padding: 50px 70px;
    border: 5px solid #fff;
    background-color: rgba(16, 16, 16, 1);
    nav {
      display: flex !important;
      flex-direction: column;
      align-items: center;
      .link {
        margin: 12px 0;
        display: block;
      }
    }
  }
}

@media (max-width: 450px) {
  .login {
    .left {
      // margin-top: 30px;
      // margin-bottom : 42px;
      .top {
        width: 111% !important;
        .logo img{
          width: 48% !important;
        }
      }
    }
    }
    .login {
      .left {
        .main {
          .steam {
            .steam_text {
              width: 60%;
            }
          }
        }
      }
    }


  .burgerMenu {
    right: 20px !important;
    padding: 30px;
  }

  .login {
    .burgerMenuButton {
      margin-right: 10px;
      margin-top: 4px;
      img {
        width: 50% !important;
      }
    }
    .left {
      width: 90% !important;

      .main {
        h5 {
          font-size: 15px !important;
          width: 70%;
        }

        h3 {
          font-size: 25px !important;
        }

        p {
          font-size: 15px;
        }

        a {
          font-size: 15px;
        }

        form {

        .block {
          height: 60px !important;
          margin-top: 16px !important;
          label {
            top: 10px !important;
          }
          label p {
            font-size: 10px !important;
          }

          input {
            font-size: 15px !important;
            padding-top: 7% !important;
          }

          input::-webkit-input-placeholder {
            font-size: 15px !important;
          }
        }
      }

        button {
          font-size: 16px !important;
          padding: 3%;
          height: 60px !important;
          margin-top: 15px !important;
        }

        .steam {
          p {
            font-size: 15px !important;
            height: auto;
          }

          img {
            width: 10%;
          }
        }
      }
    }
  }
}
